import React from 'react'
import { v4 as uuidv4 } from 'uuid';

import ResultCard from './ResultCard';

export default function ResultContainer({ results }) {
    return (
        <div className="mb-12">
            <section className="text-gray-700 body-font">
                <div className="container px-8 mx-auto lg:px-4">
                    <div className="flex flex-wrap text-center">
                        {results.map(result => {
                            if (typeof result.kanji === 'undefined') {
                                result.kanji = result.reading;
                                result.reading = "";
                            }
                            return <ResultCard key={uuidv4()} reading={result.reading} kanji={result.kanji} def1={result.def1} def2={result.def2} details={result.details}/>
                        })}
                    </div>
                </div>
            </section>
        </div>
    )
}
