import { useState, useRef } from 'react';
import axios from 'axios';

import ResultContainer from '../Results/ResultContainer';
import SearchForm from '../Search/SearchForm';

export default function Home() {
    const [searchOutput, setSearchOutput] = useState([]);
    const [searchTerm, setSearchTerm] = useState('');
    const [success, setSuccess] = useState(true);
  
    const resultRef = useRef();
  
    function scorllToResults() {
      resultRef.current.scrollIntoView({ behavior: 'smooth' });
    }
  
    function search(e) {
      setSearchOutput([]);
      e.preventDefault();
      if (!searchTerm){ return }
      const url = `/api/${searchTerm}`;
      axios
        .get(url, {
          baseURL: 'http://localhost:5000',
        })
        .then(res => {
          const data = res.data.data;
          if (data.length < 1) {
            return setSuccess(false);
          } else {
            setSuccess(true);
          }
          data.map(current => {
            return setSearchOutput(prevResults => {
              scorllToResults();
              return [
                ...prevResults,
                {
                  reading: current.japanese[0].reading,
                  kanji: current.japanese[0].word,
                  def1: current.senses[0].english_definitions[0],
                  def2:
                    typeof current.senses[1] !== 'undefined'
                      ? current.senses[1].english_definitions[0]
                      : '',
                  details: 'https://jisho.org/word/' + current.japanese[0].word
                }
              ];
            });
          });
        })
        .catch(e => console.log('error', e));
    }
  
    return (
      <div className="App">
        <div className="container mx-auto pb-24 pt-32 px-8 md:mt-32 lg:pt-6 lg:px-4">
          <div className="flex flex-col mb-36 w-full text-left lg:text-center">
            <h1 className="title-font mb-12 text-center text-blue-800 dark:text-white text-2xl font-semibold tracking-tighter sm:text-5xl">
              A Basic Japanese - English Dictionary<br/>
            </h1>
            <SearchForm
              search={search}
              searchTerm={searchTerm}
              setSearchTerm={setSearchTerm}
            />
            {!success && (
              <p className="text-center dark:text-gray-400 text-gray-500 text-xl">
                No Results
              </p>
            )}
          </div>
        </div>
        <div ref={resultRef}>
          <ResultContainer results={searchOutput} success={success} />
        </div>
      </div>
    );
  }
