import React from 'react'

export default function SearchBar({ searchTerm, onSearchChange }) {
    return (
        <div>
            <input  
                type="text"
                id="search" 
                placeholder="English, Japanese, Romaji, words or text" 
                value={searchTerm}
                onChange={(e) => { onSearchChange(e.target.value) }}
                onFocus={(e) => e.target.value = ""}
                className="w-full px-4 py-2 mb-4 mr-4 text-base placeholder-gray-400 text-gray-700 bg-gray-100 dark:bg-white border-transparent rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-700 focus:border-transparent" />
        </div>
    )
}
